

import { createStore } from 'vuex' // 모든 컴포넌트가 store 내부 데이터 공유

const store = createStore({
  state(){
    return {
      name : "kim",
      age : 20,
      likes : 34,
      clickcLikes : 0, // didn't click likes 
      followState : 0,
      followNow : "팔로잉",
      unFollow : "팔로잉 중",
  
    }
  },
  mutations : {                 // 데이터 수정 영역 - 1순위
      changeName(state){
         state.name = 'Park';   // change user's name
      },
      plusAge(state){           // plus age
          state.age++;
      }, 
      controlLikes(state){        // likes +-
          if(state.clickcLikes == 0 ){ // didn't click likes
            state.likes++;
            state.clickcLikes = 1; // click likes
          } else {
            state.likes--;
            state.clickcLikes = 0;
        }
      },
      
  },
})

export default store;