<template>
  <div
    @click="sendFilter"
    :class="filterList"
    class="filterInner"
    :style="`background-image:url(${propImg})`"
  >
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: "Filters",
  props: {
    propImg: String,
    filterList: Array,
  },
  methods: {
    sendFilter() {
      this.emitter.emit("filterToApp", this.filterList);
    },
  },
};
</script>

<style>
.filterInner {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
  background-position: center;
}
</style>