<template>

  <header>
  <div class="headerContainer">

    <h2 @click="moveTap = 0" class="logo">SELFPICK</h2> <!-- logo-->



    <ul class="header-icon">
        <li @click="moveTap = 0"><i class="fas fa-home"></i></li>
        <li @click="moveTap = 3"><i class="fas fa-people-arrows"></i></li>
        <li v-if="moveTap == 0"><input @change="uploadImg" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus"><i class="fas fa-upload"></i></label></li>
      <li v-if="moveTap == 3"><input @change="uploadImg" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus"><i class="fas fa-upload"></i></label></li>
      <li v-if="moveTap == 4"><input @change="uploadImg" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus"><i class="fas fa-upload"></i></label></li>
       <!-- upload imgFIle -->
      
        <li @click="moveTap++" v-if="moveTap == 1"><i class="fas fa-arrow-circle-right"></i></li>
        <li @click="moveTap--" v-if="moveTap == 2"><i class="fas fa-undo"></i></li>
        <li @click="publishPost" v-if="moveTap == 2"><i class="fas fa-arrow-circle-right"></i></li>
        <li @click="moveTap = 4"><img src="./assets/ChoGyeongMoon.jpg"></li>
    </ul>
  </div>
  <back-to-top text="Top" visibleoffset="500" bottom="48px" right="50px"></back-to-top>
  </header>


  <div id="contentsWrap"> <!-- 컨텐츠 전체를 감싸는 wrap -->

  <div id="leftWrap">

  <Container
    @write="writePost = $event"
    :post="post"
    :moveTap="moveTap"
    :propImg="propImg"
    :users="users"
  />
 

  </div> <!-- leftWrap end -->

  <div id="rightWrap"  v-if="moveTap == 0">

    <MyPage
    @goOtherPersonalPage="moveTap=4"
    @goFollowPage="moveTap=3"
    />

  </div>
  </div> <!-- contentsWrap end -->


  <div class="footer">
    <div class="footerContainer">
    <ul class="footer-moreview">
       <button v-if="moveTap == 0" @click="moreview">Moreview</button>
    </ul>
    </div>
  </div>

</template>






<script>


import Container from "./components/Container";
import MyPage from "./components/MyPage";
import Userdata from "./instaData/userData.js"
import Postdata from "./instaData/postData.js"; // 게시물 데이터
import axios from "axios";
import {mapState} from 'vuex';

export default {
  name: "App",
  data() {
    return {
      post: Postdata, // 게시글 추가 - JSON 형식
      morepost: 1, // more btn
      moveTap: 0, // 페이지 이동
      propImg: "", // url 이미지 하위 컴포넌트로
      writePost: "", // 내가 발행한 글
      chooseFilter: "", // 선택한 필터
      users: Userdata,
    };
  },
  components: {
    Container,
    MyPage,
  },
  
  mounted() {
    this.emitter.on("filterToApp", (a) => {
      // 필터 클릭 시 App.vue 로 이동되는 함수
      this.chooseFilter = a;
    });
  },




  methods: {
    moreview() {
      // more view 클릭 시 실행되는 함수
      axios
        .get(`https://amooonsen.github.io/vue_json/morepost${this.morepost}.json`) // json location
        .then((result) => { 
          this.post.push(result.data);
          this.morepost++;
        })
        .catch(() => {
          alert("마지막으로 불러온 페이지입니다.");
        });
    },
    uploadImg(e) {
      let imgFile = e.target.files; // @change input tag 변수 선언
      console.log(imgFile);
      let imgUrl = URL.createObjectURL(imgFile[0]); // img 넣으면 웹에 img 생성
      this.propImg = imgUrl; // 변수 imgUrl을 데이터로 바꿔 하위 컴포넌트로 전달
      this.moveTap = 1;
    
    },
    publishPost() {
      let myPost = {
        name: "user1234",
        userImage: "https://plceimg.com/100/100/arch",
        postImage: this.propImg,
        likes: 36,
        date: "방금 전",
        liked: false,
        content: this.writePost,
        filter: this.chooseFilter,
        hashTag:"",
      }; // 내 게시물
      this.post.unshift(myPost);
      this.moveTap = 0; // main page로 회귀
    },
    logoClick() {
      this.moveTap = 0;
    },


  }, // methods end



  computed : { // 연산결과 저장 공간 혹은 축약어
    
    ...mapState(['name', 'age', 'likes']), // get from Vuex 'store.js
  },
}; // export end
</script>







<style>
@import "app.css";
@import "post.css";

</style>

<style scoped>
@media (min-width: 0px) and (max-width:1050px) {
  #rightWrap {display: none;}
  #leftWrap {margin:0 auto;}  
}
</style>
