<template>

  
  <div class="post">
    <div class="post-header">
      <div class="profile"  :style="{ backgroundImage: `url(${post.userImage})` }"></div>
      <span class="profile-name">{{ post.name }}</span>
    </div>
    <div
    @dblclick="$store.commit('controlLikes')"
      :class="post.filter"
      class="post-body"
      :style="{ backgroundImage: `url(${post.postImage})` }"
    ></div>


    <div class="post-content">
      <ul class="post-action">
        <li><i class="far fa-heart"></i></li>
        <li><i class="fas fa-heart"></i></li>
        <li><i class="far fa-comment"></i></li>
      </ul>
      <p class="post-like"> 좋아요 {{ $store.state.likes }} 개 </p> <!-- bring the store -->
      <p>
        <strong>{{ post.name }}</strong> {{ post.content }}
      </p>
      <p class="hashTag">{{post.hashTag}}</p>
      <p class="date">{{ post.date }}</p>
      
    </div>
  </div>
</template>


<script>
export default {
  name: "Post",
  props: {
    post: Object,
  },
  mounted() {
    this.emitter.on("filterToApp", (a) => {
      // 필터 클릭 시 App.vue 로 이동되는 함수
      this.chooseFilter = a;
    });
  },
};
</script>




