<template>
  <div class="containerWrap">
    <div v-if="moveTap == 0" class="postWrap">

      <div class="uploadPost">
        Upload your image
  </div>

      <Post
        :post="post[i]"
        :chooseFilter="chooseFilter"
        v-for="(a, i) in post"
        :key="i"
      />
    </div>

    <!-- filter -->
    <div class="filterPage" v-if="moveTap ==1 ">
    <div class="filterWrap" v-if="moveTap == 1">
      <!-- main Img -->
      
      <div
        :class="chooseFilter"
        class="upload-image"
        :style="`background-image:url(${propImg})`"
      ></div>

      <div class="filters">
        <Filters
          :propImg="propImg"
          :filterList="filterList"
          v-for="filterList in filterArray"
          :key="filterList"
        >
          <p>{{ filterList }}</p>

          
        </Filters>  

        <!-- 필터 박스들 -->
      </div>
    </div> <!-- filterWrap -->

    <div class="filterDetail" v-if="moveTap == 1">
          <ul class="filterList">
            <li>밝기</li>
            <li>명암</li>
            <li>선명하게</li>
            <li>채도</li>
          </ul>
          </div>
    </div>
    <!-- filter end -->

    <!-- writePage -->


    <div class="writePageWrap" v-if="moveTap == 2">
      <div
        :class="chooseFilter"
        class="upload-image"
        :style="`background-image:url(${propImg})`"
      ></div>
      <div class="write">
        <textarea
          @input="$emit('write', $event.target.value)"
          class="write-box"
          placeholder="문구 입력…"
        ></textarea>
        <textarea placeholder="해쉬태그를 적어주세요!" class="hashTagTextarea"></textarea>
      </div>
    </div>

    <div class="writeChoose" v-if="moveTap == 2">
      <div class="hashtagRecommend"></div>
      <dl>
        <dt>이 달의 HashTag</dt>
        <dd>#성공적</dd>
        <dd>#백신</dd>
        <dd>#ootd</dd>
        <dd>#가을</dd>
      </dl>
      <div class="feelingRecommend">
        <ul>
          <li>Sad</li>
          <li>Happy</li>
          <li>notbad</li>
        </ul>
      </div>
    </div>
    <!-- writePage -->

  <div class="FollowPageWrap" v-if="moveTap == 3">
    <FollowPage/>
  </div>

  <div class="otherPersonalPageWrap" v-if="moveTap == 4">
    <OtherPersonalPage
    :post="post"
    :users="users"
    />
  </div>


  </div>
  <!-- container end -->
</template>





<script>
import Post from "./Post.vue";
import Filters from "./Filters.vue";
import FollowPage from './FollowPage.vue';
import OtherPersonalPage from './OtherPersonalPage.vue';

export default {
  name: "Container",
  components: {
    Post,
    Filters,
    FollowPage,
    OtherPersonalPage,
  },
  props: {
    post: Object,
    moveTap: Number,
    propImg: String,
    users: Object,
  },
  data() {
    return {
      filterArray: [
        "aden",
        "_1977",
        "brannan",
        "brooklyn",
        "clarendon",
        "earlybird",
        "gingham",
        "hudson",
        "inkwell",
        "kelvin",
        "lark",
        "lofi",
        "maven",
        "mayfair",
        "moon",
        "nashville",
        "perpetua",
        "reyes",
        "rise",
        "slumber",
        "stinson",
        "toaster",
        "valencia",
        "walden",
        "willow",
        "xpro2",
      ],
      chooseFilter: "",
    };
  },
  mounted() {
    this.emitter.on("filterToApp", (a) => {
      // 필터 클릭 시 App.vue 로 이동되는 함수
      this.chooseFilter = a;
    });
  },
};
</script>

<style>

.filterPage {width:1040px; display: flex;}
.filterWrap {width:50%; position: relative; left:0%;} /* 업로드 사진 필터 입히기, 글 쓰기 */
.filterDetail {width: 50%; height: 300px; position: relative; top:50px; right:0%; background: blue; }
.filterList {width: 100%; height: 100%; display: flex; flex-direction: column; justify-content: space-between; margin-top:30px 0;}
.filterList li {font-size: 18px; font-weight: bold;}

.upload-image {
  width: 100%;
  height: 450px;
  background: #fff;
  background-size: 50%;
  background-position: 50%,50%;
  background-repeat: no-repeat;
}
.filters {
  overflow-x: scroll;
  white-space: nowrap;
  cursor: pointer;
  height:200px;
}

.filters p {position: relative; top:100px; text-align: center; color:#333;}

.filters::-webkit-scrollbar {
  height: 10px;

}
.filters::-webkit-scrollbar-track {
  background: #f1f1f1;
}
.filters::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
  background: #555;
  
}
.write-box {
  border-top: 1px solid #ccc;
  border-bottom:none;
  border-right: none;
  border-left: none;
  width: 674px;
  height: 100px;
  padding: 15px;
  margin: auto;
  display: block;
  outline: none;
  resize:none;
}

.hashTagTextarea {width:100px; height:50px; resize: none; border:none; outline: none;}

.writeChoose {width:300px; height:400px; float:right; position: relative; left:340px; top:-500px; background-color: skyblue;}

.FollowPageWrap {width: 1100px; background: transparent; display: flex; justify-content: center;} /* 추천 팔로우 목록 */

</style>